  width: 25%;
  max-width: 66.666px;
  height: 32px;
  border-radius: 5px;
  margin: 0 7.665px;
  position: relative;
}

game-field .input:after {
  content: "";
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: -9px;
  right: -9px;
}

game-field .input--1 {
  background-color: var(--input1);
}

game-field .input--2 {
  background-color: var(--input2);
}

game-field .input--3 {
  background-color: var(--input3);
}

game-field .input--4 {
  background-color: var(--input4);
}

game-field .input--5 {
  background-color: var(--input5);
}

game-field .input--6 {
  background-color: var(--input6);
}

game-field .input--correct:after {
  background-color: #0f0;
}

game-field .input--close:after {
  background-color: #ff0;
